<template>
  <div class="login">
    <div class="loginBg">
      <div class="login-modal">
        <div>
          <h3 class="login-modal-title">
            修改密码
          </h3>
          <el-form ref="formValidate" :model="formValidate" :rules="ruleValidate" >
            <el-form-item prop="agoPass">
              <el-input v-model="formValidate.agoPass" @keyup.native.enter="handleSubmit('formValidate')" type="password" placeholder="请输入旧密码">
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input v-model="formValidate.password" @keyup.native.enter="handleSubmit('formValidate')" type="password" placeholder="请输入新密码">
              </el-input>
            </el-form-item>
            <el-form-item prop="checkPass">
              <el-input v-model="formValidate.checkPass" @keyup.native.enter="handleSubmit('formValidate')" type="password" placeholder="请重复新密码">
              </el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="login-footer">
          <el-button size="medium" :loading="modal_loading" @click="handleSubmit('formValidate')">确定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'change',
    data () {
      const validateCheckPass = (rule, value, callback) => {
        if (this.formValidate.password !== '') {
          if (value !== this.formValidate.password) {
            callback(new Error('两次输入密码不一致!'))
          } else {
            callback()
          }
        }
      }
      return {
        modal_loading: false,
        formValidate: {
          agoPass: '',
          password: '',
          checkPass: ''
        },
        ruleValidate: {
          agoPass: [
            {required: true, message: '请输入旧密码', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '请输入新密码', trigger: 'blur'}
          ],
          checkPass: [
            {validator: validateCheckPass, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleSubmit (name) { // login
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.modal_loading = true
            setTimeout(() => {
              this.modal_loading = false
              if ((this.formValidate.name === 'admin') && (this.formValidate.password === '123456')) {
                sessionStorage.token = this.formValidate.password
                this.$router.push('/index')
              } else {
                this.$message.error('账号或密码错误!')
              }
            }, 2000)
          }
        })
      },
      toGetCheckCode () {
      }
    }
  }
</script>



